.App {
	font-family: "Raleway", sans-serif;
	text-align: center;
	max-width: 1920px;
	width: 100%;
	min-height: 100%;
	height: 100%;
	margin: 0 auto;
}

html,
body {
	margin: 0;
	padding: 0;
	height: 100%;
	font-family: Raleway, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	touch-action: manipulation;

	/* Background options */
	background-color: #182731;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

h2 {
	font-family: "Raleway", sans-serif;
}

.section_div {
	width: 1400px;
	height: auto;
	padding: 5em 10em;
	margin: 5em auto;
	font-family: "Raleway", sans-serif;
	color: #ffffff;
}

.section_head {
	border-bottom: 3.5em solid #041019;
	border-right: 4em solid transparent;
	border-top-left-radius: 0.5em;
	height: 0;
	width: 25%;
}

.section_head_inner {
	width: 100%;
	height: auto;
	padding: 0.5em 0em 0.5em 2em;
	text-align: left;
}

.section_title {
	font-family: Raleway, sans-serif;
	font-size: 2em;
	font-weight: 600;
	-webkit-font-feature-settings: "pcap";
	font-feature-settings: "pcap", "pcap", "c2pc";
	font-variant: all-petite-caps;
	color: #2efdff;
}

.objective_div {
	margin: 1em auto;
	padding: 2em;
	background-color: #4acdff;
	width: 75%;
	border-radius: 2em;
}

.objective_text {
	font-size: 1.5em;
	font-weight: 600;
	color: #000000b5;
}

.project {
	width: 100%;
	height: 20em;
	background: #04121b;
	margin: 1em auto;
	overflow: hidden;
	position: relative;
}

.project_img_div {
	float: right;
	width: 70%;
	mix-blend-mode: soft-light;
	-webkit-filter: blur(0.25em) saturate(0.1);
	        filter: blur(0.25em) saturate(0.1);
	position: relative;
}

.project_img {
	width: 100%;
}

.project_details_div {
	/* Formatting */
	width: 40%;
	height: 100%;
	margin-top: 2em;
	margin-left: 2em;

	/* Aesthetic */
	background: inherit;
	box-shadow: 0 0 7em 15em #04121b;
	border-radius: 0 100em;

	/* Positioning */
	float: left;
	text-align: left;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
}

.project_title_div {
	width: 100%;
	height: auto;
	text-align: left;
	padding-left: 1em;
	padding-top: 0.5em;
}

.project_title {
	font-family: Raleway, sans-serif;
	font-size: 2em;
	font-weight: 600;
	-webkit-font-feature-settings: "pcap";
	        font-feature-settings: "pcap";
	font-variant: petite-caps;
	color: #2efdff;
}

.project_description_div {
	padding-left: 1em;
	padding-top: 0.5em;
	text-align: justify;
	font-size: 1em;
}

.tags_ribbon {
	/* Positioning */
	top: 23px;
	right: -40px;
	left: auto;
	transform: rotate(35deg);
	-webkit-transform: rotate(35deg);
	position: absolute;
	z-index: 3;

	/* Formatting */
	width: 10em;
	height: auto;
	padding: 0.5em;

	/* Font format */
	font-size: 1.25em;
	-webkit-font-feature-settings: "pcap", "c2pc";
	        font-feature-settings: "pcap", "c2pc";
	font-variant: all-petite-caps;
	font-weight: bolder;
}

.project_date_div {
	/* Formatting */
	margin-left: 0.75em;
	padding-bottom: 0.3em;

	/* Font options */
	font-size: 2em;
	font-family: monospace;
	-webkit-font-feature-settings: "pcap", "c2pc";
	        font-feature-settings: "pcap", "c2pc";
	font-variant: all-petite-caps;
	color: #00999a;
}

.master_div {
	background: #0c171f;
	text-align: left;
}

.contact_div {
	padding: 1em;
	background: #0e1a23;
	text-align: left;
	font-size: 1.25em;
	width: 50%;
	border-bottom-right-radius: 10em;
	display: inline-table;
}

.contact_info {
	margin: 0.5em 1em;
	display: inline-block;
	position: absolute;
}

.contact_icon {
	width: 2em;
	height: 2em;
	display: inline-block;
}

.contact_subdiv {
	height: 2em;
	margin: 1em;
	position: relative;
}

.icon {
	width: 100%;
}

.links_div {
	display: inline-block;
	width: 41em;
	text-align: center;
	padding: 2em 0;
	position: absolute;
	mix-blend-mode: luminosity;
}

.link_icon {
	width: 7em;
	margin: 0 2em;
}

.greeting_div {
	width: 1080px;
	height: auto;
	padding: 15em;
	padding-bottom: 20em;
	margin: 5em auto;
	font-family: "Raleway", sans-serif;
	color: #ffffff;
}

.greeting_title_div {
	border-bottom: 1px solid #7dc3de;
	padding-bottom: 0.5em;
	margin-right: 10em;
	padding-left: 1em;
	text-align: left;
}

.greeting_title {
	font-family: Raleway, sans-serif;
	font-size: 3em;
	font-weight: 900;
	-webkit-font-feature-settings: "pcap";
	        font-feature-settings: "pcap";
	font-variant: petite-caps;
	color: #beedff;
}

.greeting_message_div {
	margin-right: 10em;
	margin-left: 4em;
	color: #fff;
	font-size: 1.5em;
	text-align: justify;
}

.hl {
	color: #63e8ff;
}

